@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');

html,body,div#root {
  width:100%;
  height:100%;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans';
  overflow:hidden;
}

div.header {
	width:100%;
	display:table;
	background-color:white;
}

div.header_child {
	display:table-cell;
	width:33.33%;
	height:100%;
}


div.ficha table {
	border-spacing:0px;
	font-size:14px;
}

div.ficha table td:nth-child(1) {
	font-weight:600;
}

div.ficha table td:nth-child(2) {
	font-weight:300;
}

div.ficha table td {
	text-align:center;
	padding:none;
	width:50%;
	font-size:12px;
	max-height:20px;
	min-height:20px;
}

div.ficha button {
	border:none;
	font-family:'Open Sans';
	font-weight:600;
	background-color:rgb(13,190,190);
	border: 0px solid lightGray;
	color:white;
	border-radius:2px;
	min-height:40px;
	cursor:pointer;
}


div.button {
	background-color:WhiteSmoke;
	border-bottom:1px solid lightGray;
	position:relative;
	color:gray;
}

div.button:hover {
	cursor:pointer;
	color:rgb(40,40,40);
}

div.selectedButton {
	background-color:white;
	border-bottom:none;
	font-weight:800;
	position:relative;
	color:black;
}

div.espacioBlanco {
	border-bottom:1px solid lightGray;
}

option,input {
	font-family:'Open Sans';
	font-weight:300;
	text-align:center;
}

div#titulo {
	/*background-color:rgba(13,180,190,0.1);*/
	/*border-top:1px solid lightGray;*/
}

div#titulo:hover {
	/*
	background-color:rgba(13,180,190,0.2);
	text-decoration: underline;
	*/
}

div#bubble {
/*	
	max-width:150px;
	min-width:100px;
*/
	padding:5px;
	height:25px;
	position: relative;
	background-color:black;
	color:white;
	border-radius: .4em;
	text-align:center;
	font-size:12px;
	vertical-align:middle;
	display:table-cell;
	font-weight:600;
}

div#bubble:after {
	content: '';
	position: absolute;
	top: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 8px solid transparent;
	border-bottom-color: black;
	border-top: 0;
	margin-left: -8px;
	margin-top: -8px;
}

.polygonTooltip {
	background-color:black;
	border-color:black;
	color:white;
	font-family:'Open Sans';
	font-weight:600;
}

.leaflet-tooltip-left.polygonTooltip::before {
	border-left-color:black;
}

.leaflet-tooltip-right.polygonTooltip::before {
	border-right-color:black;
}


